import React from 'react';

class Greet extends React.Component {
	
	constructor(props){
		super(props);
		
		this.state = {val: "awesome"};
	}
	
	
	changeValue = () => {
		this.setState(
			{val: "wonderful"}
			);
		}
	
	render(){
		return(
			<div>
				<h1>About Me</h1>
			</div>
		);
	}
}


export default Greet;