import React from 'react';
import { Media } from 'reactstrap';
import '../../assets/css/media.css';

class Menu extends React.Component {
	constructor(props){
		super(props);
		
		this.state = {};
	}
	
	render(){
		
		const menu = this.props.places.map((place) => {
			return(
				<div key={place.id} id="unit" className="col-12 mt-5">					
							<div><strong>{place.name}</strong></div>
							<div><strong>Instructor - </strong>{place.category}</div>
							<div><strong>Info - </strong>{place.description}</div>
							<div>Info source - {place.source}</div>
				</div>
			);
		});
		
		return(
			<div className="container">
				<div className="row">
					<Media list>
						{menu}
					</Media>
				</div>
			</div>
		);
	}
}

export default Menu;